import { Grid } from '@material-ui/core'
import { PushToTalkButton, PushToTalkButtonContainer, ErrorPanel } from '@speechly/react-ui'
import React, { useEffect, useRef } from 'react'
import { SpeechState, useSpeechContext } from '@speechly/react-client';

import Main from './components/main/main'
import Details from './components/Details/Details'
import useStyles from './styles';
import Title from './components/Title/Title.jsx'



const App = () => {
    const classes = useStyles();
    const { speechState } = useSpeechContext();
    const main = useRef(null)

    const executeScroll = () => main.current.scrollIntoView();

    useEffect(() => {
        if(speechState === SpeechState.Recording) {
            executeScroll();
        }
    },[speechState])

    return (
        <div>
            <Grid className={classes.title} item xs={12} >
                <Title />
            </Grid>
            <Grid className={classes.grid} spacing={2} container alignItems="center" justify="flex-end" style={{ height: '100vh'}}>

                <Grid item xs={12} sm={4} className={classes.display} justify="center" alignItems="center" style={{margin: '0 auto'}}>
                    <Details title="Income" />
                    <Details title="Expense" />
                </Grid>
                <Grid ref={main} item xs={12} sm={4} className={classes.main} justify="flex-end" alignItems="flex-end" style={{ marginLeft: 'auto', marginRight: 'auto'  }} >
                    <Main />
                </Grid>
                <Grid item xs={12} sm={4} className={classes.mobile} justify="center" alignItems="center" style={{margin: '0 auto'}}>
                    <Details title="Income" />
                    <Details title="Expense" />
                </Grid>
                <PushToTalkButtonContainer>
                    <PushToTalkButton className={classes.button}/>
                </PushToTalkButtonContainer>
            </Grid>
        </div>
    )
}

export default App